import {
	FETCHED_FRIENDS,
	UPDATE_FRIENDS,
	FETCHED_INSTAGRAM_FRIENDS,
	UPDATE_INSTAGRAM_FRIENDS,
	FETCHED_BANNED_FRIENDS,
	UPDATE_BANNED_FRIENDS,
} from './actionType';

const userInitialState = {
	users: [],
	instagramUsers: [],
	bannedUsers: [],

	offsetFriends: 0,
	offsetInstagramFriends: 0,
	offsetBannedFriends: 0,
};


export default function friends (state = userInitialState, action = {}) {
	switch (action.type) {
		case FETCHED_FRIENDS:
			return {
				...state,
				offsetFriends: action.offsetFriends,
				users: [...action.users],
			};
		case UPDATE_FRIENDS:
			return {
				...state,
				offsetFriends: action.offsetFriends,
				users: [...state.users, ...action.users],
			};
		case FETCHED_INSTAGRAM_FRIENDS: 
			return {
				...state,
				offsetInstagramFriends: action.offsetInstagramFriends,
				instagramUsers: [...action.users.filter(user => user.instagram)],
			};
		case UPDATE_INSTAGRAM_FRIENDS:
			return {
				...state,
				offsetInstagramFriends: action.offsetInstagramFriends,
				instagramUsers: [...state.instagramUsers, ...action.users.filter(user => user.instagram)],
			};
		case FETCHED_BANNED_FRIENDS: 
			return {
				...state,
				offsetBannedFriends: action.offsetBannedFriends,
				bannedUsers: [...action.users.filter(user => user.deactivated === 'deleted' || user.deactivated === 'banned')]
			};
		case UPDATE_BANNED_FRIENDS:
			return {
				...state,
				offsetBannedFriends: action.offsetBannedFriends,
				bannedUsers: [...state.bannedUsers, ...action.users.filter(user => user.deactivated === 'deleted' || user.deactivated === 'banned')]
			};
		default:
			return state;
	};
};

export const getFriends = (state) => state.friends.users;
export const getFriendsInInstagram = (state) => state.friends.instagramUsers;
export const getBannedFriends = (state) => state.friends.bannedUsers;

export const getOffsetFriends = (state) => state.friends.offsetFriends;
export const getOffsetInstagramFriends = (state) => state.friends.offsetInstagramFriends;
export const getOffsetBannedFriends = (state) => state.friends.offsetBannedFriends;