import {
	FETCHED_FRIENDS,
	UPDATE_FRIENDS,
	FETCHED_INSTAGRAM_FRIENDS,
	UPDATE_INSTAGRAM_FRIENDS,
	FETCHED_BANNED_FRIENDS,
	UPDATE_BANNED_FRIENDS
} from './actionType';
import {getFriends} from '../../utils/api/vk';

export const fetchedAllFriends = (setFetching) => {
	let offset = 0;
	const fetched = async (dispatch) => {
		const data = await getFriends(100, offset);
		const isVoidData = !data.length;
		if (!isVoidData && offset <= 5000) {
			console.log('fetchedAllUsers', offset);
			offset += 100;
			fetched(dispatch);
			dispatch({
				type: UPDATE_FRIENDS,
				users: [...data],
				offsetFriends: offset
			});
			if (setFetching) { setFetching(); }
		}
	};
	return fetched;
};

export const fetchedFriends = (setFetching) => {
	let offset = 0;
	const fetched = async (dispatch) => {
		const data = await getFriends(100, offset);
		const isVoidData = !data.length;
		if (data.length < 10 && offset <= 5000) {
			console.log('FETCHED_FRIENDS', offset);
			offset += 100;
			fetched(dispatch);
		}
		if (!isVoidData) {
			dispatch({
				type: UPDATE_FRIENDS,
				users: [...data],
				offsetFriends: offset
			});
			if (setFetching) { setFetching(); }
		}
	};
	return fetched;
};

export const fetchedInstagramFriends = (setFetching) => {
	let offset = 0;
	const fetched = async (dispatch) => {
		const data = await getFriends(100, offset);
		const instagramFriends = data.filter(user => user.instagram);
		const isVoidData = !instagramFriends.length;
		if (instagramFriends.length < 10 && offset <= 5000) {
			console.log('FETCHED_INSTAGRAM_FRIENDS', offset);
			offset += 100;
			fetched(dispatch);
		}
		if (!isVoidData) {
			dispatch({
				type: UPDATE_INSTAGRAM_FRIENDS,
				users: [...instagramFriends],
				offsetInstagramFriends: offset
			});
			if (setFetching) { setFetching(); }
		}
	};
	return fetched;
};

export const fetchedBannedFriends = (setFetching) => {
	let offset = 0;
	const fetched = async (dispatch) => {
		const data = await getFriends(100, offset);
		const bannedFriends = data.filter(user => user.deactivated === 'deleted' || user.deactivated === 'banned');
		const isVoidData = !bannedFriends.length;
		if (bannedFriends.length < 10 && offset <= 5000) {
			console.log('FETCHED_BANNED_FRIENDS', offset);
			offset += 100;
			dispatch({
				type: UPDATE_BANNED_FRIENDS,
				users: [...bannedFriends],
				offsetBannedFriends: offset
			});
			fetched(dispatch);
		}
		if (!isVoidData) {
			dispatch({
				type: UPDATE_BANNED_FRIENDS,
				users: [...bannedFriends],
				offsetBannedFriends: offset
			});
			if (setFetching) { setFetching(); }
		}
	};
	return fetched;
};

export const updateFriends = (offset=0) => async (dispatch) => {
	console.log('UPDATE_FRIENDS', offset);
	const data = await getFriends(100, offset);
	dispatch({type: UPDATE_FRIENDS, users: [...data]});
};

export const updateInstagramFriends = (offset=0) => async (dispatch) => {
	console.log('UPDATE_INSTAGRAM_FRIENDS', offset);
	const data = await getFriends(100, offset);
	dispatch({type: UPDATE_INSTAGRAM_FRIENDS, users: [...data]});
};

export const updateBannedFriends = (offset=0) => async (dispatch) => {
	console.log('UPDATE_BANNED_FRIENDS', offset);
	const data = await getFriends(100, offset);
	let x = offset + 100;
	dispatch({type: UPDATE_BANNED_FRIENDS, users: [...data], offsetBannedFriends: x});
};